export default {
	data() {
		return {
			count:'',
			str:'获取验证码'
		};
	},
	methods: {
		sendCode(){
			this.count=true;
			if(this.str!='获取验证码'){
				return;
			}
			let seconds=60;
			let clear=setInterval(()=>{
				seconds--;
				this.str=seconds+'s';
				if(seconds<=0){
					clearInterval(clear);
					this.str='获取验证码';
					this.count=false;
				}
			},1000)
		},
		getCode() {
			if(this.str!='获取验证码'){
				return;
			}
			this.$emit('getCode')		
		}
	}
};
